import { Component, h, Host } from '@stencil/core';

@Component({
  tag: 'mt-paragrafo',
  styleUrl: 'paragrafo.scss',
  scoped: true,
})
export class Paragrafo {
  render() {
    return (
      <Host class="mt-paragrafo">
        <p>
          <slot></slot>
        </p>
      </Host>
    );
  }
}
