$mt-breakpoints: (
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px,
  xxl: 1400px,
);

:host {
  --mt-paragrafo-padding: 0;
  --mt-paragrafo-margin: 0;

  p {
    color: #000;
    font-family: 'Merriweather', serif;
    font-weight: 400;
    font-size: 13px;
    line-height: 1.3;
    margin: var(--mt-paragrafo-margin);
    padding: var(--mt-paragrafo-padding);
  }
}

::slotted(u) {
  text-decoration-color: #9abec0;
}

::slotted(ul) {
  padding-left: 32px;
}
